.header {
  width: 100%;
  z-index: 5;
  opacity: .9;
  background: #10101d;
  position: fixed;
  top: 0;
}

.header .wrapper {
  justify-content: space-between;
  display: flex;
}

.header-logo {
  color: #fff;
  padding: 33.5px 0;
  font-family: Raleway, sans-serif;
  font-size: 28px;
  font-style: normal;
  font-weight: 800;
  line-height: 33px;
}

@media (max-width: 820px) {
  .header-logo {
    font-size: 24px;
    line-height: 30px;
  }
}

.header-nav__list {
  justify-content: space-between;
  display: flex;
}

@media (max-width: 768px) {
  .header-nav__list {
    display: none;
  }
}

.header a {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  margin-left: 60px;
  font-size: 13px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.header a:hover {
  color: #43abf0;
}

.header a:active {
  color: #43abf0;
  text-align: center;
  -o-transition: .5s;
  border-bottom: 3px solid #43abf0;
  padding-bottom: 10px;
  transition: all .5s;
}

.header li {
  padding: 43px 0 39px;
  list-style-type: none;
}

.header-burger {
  display: none;
}

@media (max-width: 768px) {
  .header-burger {
    width: 24px;
    height: 18px;
    cursor: pointer;
    margin: 39px 15px 0 0;
    display: block;
    position: relative;
  }
}

@media (max-width: 768px) and (max-width: 460px) {
  .header-burger {
    margin: 33.5px 15px 0 0;
  }
}

@media (max-width: 768px) {
  .header-burger span {
    top: 16px;
  }

  .header-burger:before {
    top: 8px;
  }

  .header-burger:after, .header-burger:before {
    content: "";
  }

  .header-burger:after, .header-burger:before, .header-burger span {
    height: 3px;
    width: 100%;
    background: #fff;
    position: absolute;
  }
}

