.ordering-about__dot:before, .ordering-about__dot:after {
  content: "";
  height: 6px;
  width: 6px;
  background: #fdfdfe;
  border-radius: 50%;
  display: block;
  position: absolute;
}

.ordering-about__dot:before {
  left: 25px;
}

.ordering-about__dot:after {
  right: 25px;
}

.ordering-about .icon:before, .ordering-about .icon:after {
  content: "";
  height: 6px;
  width: 6px;
  background: #fdfdfe;
  border-radius: 50%;
  display: block;
  position: absolute;
  bottom: 43px;
}

@media (max-width: 1200px) {
  .ordering-about .icon:before, .ordering-about .icon:after {
    display: none;
  }
}

.ordering-about .icon:before {
  left: 120%;
}

.ordering-about .icon:after {
  right: 125%;
}

.ordering-about .icon-first:before, .ordering-about .icon-last:after {
  content: "";
  height: 6px;
  width: 6px;
  background: #fdfdfe;
  border-radius: 50%;
  display: block;
  position: absolute;
  bottom: 43px;
}

@media (max-width: 1200px) {
