.ordering-form__input {
  width: 100%;
  max-width: 370px;
  color: #272733;
  background: #dddddf;
  border: 1px solid #fff;
  border-radius: 3px;
  outline: 0;
  padding: 12px 10px;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
}

.ordering-form__input::-webkit-input-placeholder {
  color: #272733;
}

.ordering-form__input:-ms-input-placeholder {
  color: #272733;
}

.ordering-form__input::-moz-placeholder {
  color: #272733;
}

.ordering .select {
  width: 100%;
  max-width: 370px;
  position: relative;
  top: -2px;
}

.ordering .select-toggle {
  display: none;
}

.ordering .select-native, .ordering .select-custom {
  border-radius: 3px;
  position: relative;
}

.ordering .select-custom {
  appearance: none;
  position: absolute;
  top: 0;
  left: 0;
}

.ordering .select-custom.isActive .select-elem__title {
  color: #fff;
  background: #3d4050;
}

.ordering .select-custom.isActive .select-elem__title:after {
  background-image: url("up.4bac23d0.svg");
  transform: translateY(-50%)rotate(0deg);
}

.ordering .select-custom.isActive .select-elem__options {
  visibility: visible;
  opacity: 1;
  top: 44px;
}

.ordering .select-native {
  -o-transition: border-radius .2s ease, color .2s ease;
  border-radius: 3px;
  padding: 8px 10px;
  transition: border-radius .2s, color .2s;
}

.ordering .select-elem {
  width: 100%;
}

.ordering .select-elem__title {
  text-align: left;
  -o-transition: border-radius .2s ease;
  padding: 13px 10px;
  transition: border-radius .2s;
}

.ordering .select-elem__title:after {
  -o-transition: transform .2s ease;
  content: "";
  height: 5px;
  width: 9px;
  background: url("down.0789bf62.svg") center no-repeat;
  transition: transform .2s;
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%)rotate(360deg);
}

.ordering .select-elem__options {
  text-align: left;
  z-index: 5;
  max-height: 180px;
  visibility: hidden;
  opacity: 0;
  -o-transition: opacity .2s ease, visibility .2s ease;
  scrollbar-width: thin;
  scrollbar-color: #3d4050 #43abf0;
  background: #272733;
  border: 1px solid #3d4050;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  transition: opacity .2s, visibility .2s;
  position: absolute;
  left: 0;
  right: 0;
  overflow-y: scroll;
}

.ordering .select-elem__options::-webkit-scrollbar {
  width: 7px;
  border-radius: 5px;
  margin: 3px;
}

.ordering .select-elem__options::-webkit-scrollbar-track {
  background-color: #3d4050;
}

.ordering .select-elem__options::-webkit-scrollbar-thumb {
  background: #43abf0;
  border-radius: 5px;
}

.ordering .select-elem__options-item {
  color: #fff;
  cursor: pointer;
  -o-transition: color .2s ease;
  padding: 7px 10px;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  transition: color .2s;
}

.ordering .select-elem__options-item.isHover, .ordering .select-elem__options-item:hover {
  color: #43abf0;
}

.ordering .select-native, .ordering .select-elem__title {
  width: 100%;
  color: #3d4050;
  cursor: pointer;
  background: #dddddf;
  border-radius: 3px;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
}

@media (hover: hover) {
  .ordering .select-custom {
    display: block;
  }
}

